:root {
    --section-overlay: rgb(0, 0, 0, 0.7);
    --background-overlay: rgb(0, 0, 0, 0.2)
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.app {
    width: 100%;
    height: 100vh;
    background-size: cover;
    background-position: center;
    background-repeat: repeat-y;
}

.overlay {
    width: 100%;
    height: 100vh;
    background-color: var(--background-overlay);
}

.container {
    max-width: 800px;
    margin: auto;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    gap: 1rem;
    padding: 1rem;
}

.section {
    width: 100%;
    text-align: center;
    padding: 1rem;
    border-radius: 0.4rem;
    color: white;
}

.section_inputs, .section_temperature {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: var(--section-overlay);
}

.section_inputs> #error_message>input {
    border: 0.8px solid white;
    border-radius: 0.4rem;
    background-color: transparent;
    color: white;
    padding: 0.5rem;
    font-size: 20px;
    font-weight: 200;
}

.section_inputs> #error_message> input {
    outline: none;
}

.error {
    color: red;
    font-size:x-large;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;

}

.section_inputs>button {
    padding: 10px 50px;
    border: none;
    border-radius: 0.4rem;
    font-size: 20px;
    font-weight: 500;
    background-color: white;
    cursor: pointer;
    transition: 0.5s;
}

.section_inputs>button:hover {
    background-color: lightgray;
}

.icon {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.icon>h3 {
    font-size: 15px;
    font-weight: 200;
    text-transform: capitalize;
}

.temperature> h1 {
    font-size: 60px;
}

#error_message> p {
    color: red;
    padding-bottom: 5px;
    
}

@media screen and (max-width: 600px) {
    .section_inputs> #error_message> input {
        width: 200px;
    }
    .section_inputs>button {
        padding: 10px 20px;
    }
}